*{
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  background-color: whitesmoke;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

textarea {
  background-color: transparent;
}
.app{
  background-color: whitesmoke;
  width: 100vw;
  height: 100%;
}

body::-webkit-scrollbar {
  display: none;
}
div{
  box-sizing: border-box;
}
.login{
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: #f2f2f2;
}

.login__container{
    display: flex;
    flex-direction: column;
    text-align:center;
}

.login__container > img{
    margin-bottom: 5px;
    object-fit: contain;
    width: 200px; 
}

.login__container > h4{
    padding: 0;
    margin-bottom: 40px;
    color: navy;
    font-size: large;
    font-weight: 900;
}

.login button:last-child {
    margin-top:11px;
}
.card {
    margin: 15px 10px;
    padding: 0;
    border-radius: 16px;
    position: relative;
    overflow: hidden;
    width: 280px;
    display: inline-block;
    background-color:grey;
}

.card img, video, embed{
    width: 100%;
    height: 300px;
}

.content{
    position: absolute;
    bottom: 0px;
    background-color: grey;
    width:300px;
    padding: 6px;
}

.content a{
    color: white;
    background-color: black;
    text-decoration: none;
    border-radius: 6px;
    padding:0 2px;
}

.header{
    display: flex;
    flex: 1 1;
    align-items: center;
    justify-content:safe;
}

.meta{
    font-size:x-small;
    position: absolute;
    right: 38px;
}

.description{
    display: flex;
    position: relative;
}

.description a{
    position: absolute;
    right: 28px;
}

.card__mainContent{
    top: 0;
    position: relative;
    align-items: center;
    justify-content: flex-start;
    display: flex;
}


audio{
    display: grid;
}

.audiofile{
    background-color: white;
    display: flex;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    flex-direction: column;
    padding: 12px;
    margin: 8px;
    border-radius: 12px;
}

.audio__meta{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.audio__footer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.audio__footer > a{
    color: white;
    text-decoration: none;
    padding: 1px 4px;
    background-color: black;
    border-radius: 6px;
}

.navbar{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between; 
    padding: 10px;
    position: -webkit-sticky;
    position: sticky; 
    top: 0; 
    background: #d6249f;
    background: radial-gradient(circle at 30% 107%, #fd5949 15%,#d6249f 45%,#285AEB 90%);
    box-shadow: 0px 3px 10px rgba(0,0,0,.25);
    width: 100%;
    z-index: 10 ;
}

.navbar__left{
    flex: 0.2 1;
}

.navbar__center{
    flex: 0.5 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navbar__right{
    flex: 0.3 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.navbar__right > button {
    margin-right: 20px;
}

.navbar__navlink{
    text-decoration: none;
    font-size: 16px;
    font-weight: 800;
    color: rgba(255,255,255,.60);
}

.navbar__navlink:hover{
    font-size: 20px;
    transform: scale(1.1);
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    color: white;
}

.navbar__active{
    color: white;
    font-size: 20px;
}

.navbar__right button{
    border: 1px solid white;
    color: white;
}

.upload{
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: auto;
    vertical-align: middle;
    margin-top: 100px;
    border-radius: 14px;
}

.upload__form{
    border-radius: 14px;
    display: flex;
    flex-direction: column;
    background-color:white;
    flex: 0.5 1;
    box-shadow: 0px 3px 10px rgba(0,0,0,.25);
}

.upload__form > form {
    display: flex;
    flex: 1 1;
    align-items: center;
    padding: 20px;
}

.upload__formLeft{
    flex: 0.4 1;
    background-color: white;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
    border-right: 1px solid grey;
}

.upload__formRight{
    flex: 0.6 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 12px;
    margin: 12px;
    justify-content: space-between;
}

input {
    border: none;
    outline: none;
    padding: 6px;
    margin: 4px;
    font-size: 18px;
}

.upload__formRight button{
    background: radial-gradient(circle at 30% 107%, #fd5949 5%,#d6249f 35%,#285AEB 80%);
    box-shadow: 0px 3px 10px rgba(0,0,0,.25);
    width: 100%;
    border-radius: 8px;
    border: none;
    padding: 8px;
    color: white;
}

#input-file{
    display: none;
    opacity: 0;
    width: 0;
}

#icon-button:hover{
    background-color: transparent;
}
.homepage{
    margin: 20px 6px;
    width: 100%;
    height: 100%;
}

.profile{
    height: 100%;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
}

.profile__user{
    /* margin-top: -100px; */
    display: flex;
    flex-direction: column;
    width: 40%;
    align-items: center;
    justify-content: center;
    background: #d6249f;
    background: radial-gradient(circle at 50% 50%, #fd5949 15%,#d6249f 105%,#285AEB 95%);
    box-shadow: 0px 3px 10px rgba(0,0,0,.25);
    border-radius: 15px;
    padding-top: 20px;
}

.profile__details{
    margin: 8px;
    padding: 6px 0;
    color: white;
    font-size: 18px;
    font-weight: 600;
}

.profile__detailsItem {
    margin: 6px 0;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 5px 0;
    flex: 1 1;
}

.profile__detailsItem > div{
    margin-top: 13px;
    text-transform: uppercase;
    text-decoration: underline;
}

.profile button {
    background: #d6249f;
    background: radial-gradient(circle at 30% 107%, #fd5949 0%,#d6249f 35%,#285AEB 85%);
    box-shadow: 0px 3px 10px rgba(0,0,0,.25);
    border-radius: 10px;
    width: 40%;
    color: white;
    margin: 20px;
    font-size: medium;
    font-weight: 500;
}

